const validateName = (name) => {
  if (name === null || name === "" || typeof name === "undefined") {
    return ["questionsStep.USER_NAME.Can_not_empty", false];
  } else if (name.length > 20) {
    return ["questionsStep.USER_NAME.Should_less_20", false];
  } else {
    return ["Success", true];
  }
};

export default validateName;
