import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./AnnualIncome.css";

function AnnualIncome({ setCurrentStep, ansAnnualIncome }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.ANNUAL_INCOME.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansAnnualIncome === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", 0);
            }}
          >
            {ansAnnualIncome === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.ANNUAL_INCOME.ans_1")}
          </div>
          <div
            className={
              ansAnnualIncome === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", 2);
            }}
          >
            {ansAnnualIncome === 2 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.ANNUAL_INCOME.ans_2")}
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansAnnualIncome === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", 1);
            }}
          >
            {ansAnnualIncome === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.ANNUAL_INCOME.ans_3")}
          </div>
          <div
            className={
              ansAnnualIncome === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", 3);
            }}
          >
            {ansAnnualIncome === 3 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.ANNUAL_INCOME.ans_4")}
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansAnnualIncome === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", 4);
            }}
          >
            {ansAnnualIncome === 4 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.ANNUAL_INCOME.ans_5")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("ANNUAL_INCOME", "HAVE_BANKRUPTCY", ansAnnualIncome);
          }}
        >
          {ansAnnualIncome !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "ANNUAL_INCOME",
              "CURRENT_EMPLOYMENT",
              ansAnnualIncome
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default AnnualIncome;
