import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./DownPayment.css";

function DownPayment({ setCurrentStep, ansDownPayment }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.DOWN_PAYMENT.head")}
          </h2>
          <h3 className="q-step-sub-text">
            {t("questionsStep.DOWN_PAYMENT.sub")}
          </h3>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansDownPayment === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 0);
            }}
          >
            {ansDownPayment === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.DOWN_PAYMENT.ans_1")}
          </div>
          <div
            className={
              ansDownPayment === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 2);
            }}
          >
            {ansDownPayment === 2 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.DOWN_PAYMENT.ans_2")}
          </div>
          <div
            className={
              ansDownPayment === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 4);
            }}
          >
            {ansDownPayment === 4 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.DOWN_PAYMENT.ans_3")}
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansDownPayment === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 1);
            }}
          >
            {ansDownPayment === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.DOWN_PAYMENT.ans_4")}
          </div>
          <div
            className={
              ansDownPayment === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 3);
            }}
          >
            {ansDownPayment === 3 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.DOWN_PAYMENT.ans_5")}
          </div>
          <div
            className={
              ansDownPayment === 5
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 5);
            }}
          >
            {ansDownPayment === 5 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.DOWN_PAYMENT.ans_6")}
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansDownPayment === 6
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("DOWN_PAYMENT", "CURRENT_EMPLOYMENT", 6);
            }}
          >
            {ansDownPayment === 6 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.DOWN_PAYMENT.ans_7")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "DOWN_PAYMENT",
              "CURRENT_EMPLOYMENT",
              ansDownPayment
            );
          }}
        >
          {ansDownPayment !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("DOWN_PAYMENT", "SERVED_MILITARY", ansDownPayment);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default DownPayment;
