import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./ServedMilitary.css";

function ServedMilitary({ setCurrentStep, ansServedMilitary, qOneAns }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.SERVED_MILITARY.head")}
          </h2>
          <h3 className="q-step-sub-text">
            {t("questionsStep.SERVED_MILITARY.sub")}
          </h3>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansServedMilitary === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "SERVED_MILITARY",
                qOneAns === 0 ? "DOWN_PAYMENT" : "CURRENT_EMPLOYMENT",
                0
              );
            }}
          >
            {ansServedMilitary === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SERVED_MILITARY.ans_1")}
          </div>
          <div
            className={
              ansServedMilitary === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SERVED_MILITARY", "MILITARY_BRANCH", 1);
            }}
          >
            {ansServedMilitary === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SERVED_MILITARY.ans_2")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "SERVED_MILITARY",
              ansServedMilitary === 0
                ? qOneAns === 0
                  ? "DOWN_PAYMENT"
                  : "CURRENT_EMPLOYMENT"
                : "MILITARY_BRANCH",
              ansServedMilitary
            );
          }}
        >
          {ansServedMilitary !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "SERVED_MILITARY",
              qOneAns === 0 ? "WHEN_PURCHASE" : "HOW_USE_HOME",
              ansServedMilitary
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default ServedMilitary;
