import React, { useState } from "react";
import Input from "react-phone-number-input/input";
import { isValidPhoneNumber } from "react-phone-number-input";

import "./UserPhoneNumber.css";
import { useTranslation } from "react-i18next";

function UserPhoneNumber({ setCurrentStep, ansUserPhoneNumber }) {
  const [phoneNumberValue, setPhoneNumberValue] = useState(ansUserPhoneNumber);
  const [haveError, setHaveError] = useState(false);

  const { t } = useTranslation();

  const nextBtnClick = () => {
    if (phoneNumberValue === null) {
      setHaveError(true);
    } else {
      if (typeof phoneNumberValue !== "undefined") {
        if (isValidPhoneNumber(phoneNumberValue)) {
          setPhoneNumberValue(phoneNumberValue);
          setCurrentStep("USER_PHONE_NUMBER", "THANK_YOU", phoneNumberValue);
          setHaveError(false);
        } else {
          setHaveError(true);
        }
      } else {
        setHaveError(true);
      }
    }
  };

  const inputOnChange = (value) => {
    if (typeof value !== "undefined") {
      if (isValidPhoneNumber(value)) {
        setPhoneNumberValue(value);
        setHaveError(false);
      } else {
        setHaveError(true);
      }
    } else {
      setHaveError(true);
    }
  };

  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.USER_PHONE_NUMBER.head")}
          </h2>
          <h3 className="q-step-sub-text">
            {t("questionsStep.USER_PHONE_NUMBER.sub")}
          </h3>
        </div>
      </div>
      <div className="row mt-5 d-flex justify-content-center">
        <div className="col-sm-3">
          <Input
            className="input-box-style"
            placeholder="( xxx ) - xxx - xxxx "
            country="US"
            maxLength="14"
            value={phoneNumberValue}
            onChange={inputOnChange}
          />
          {haveError && (
            <div className="error-message">
              {t("questionsStep.USER_PHONE_NUMBER.error")}
            </div>
          )}
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-3"
          onClick={() => {
            nextBtnClick();
          }}
        >
          <div className="step-q-button">
            {t("questionsStep.USER_PHONE_NUMBER.next")}
          </div>
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "USER_PHONE_NUMBER",
              "USER_NAME",
              ansUserPhoneNumber
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default UserPhoneNumber;
