import React from "react";
import { useTranslation } from "react-i18next";

import "./StepOne.css";

function StepOne({ setCurrentStep, qOneAns, stepOneTitle, stepOneSub }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center hero-text">
        <div className="col-sm-7">
          {stepOneTitle === "" ? "" : <h2 className="">{stepOneTitle}</h2>}
          {stepOneSub === "" ? "" : <h3 className="">{stepOneSub}</h3>}
        </div>
      </div>
      <div className="row mt-5 mb-5 d-flex justify-content-center">
        <div className="col-sm-4">
          <div
            className={
              qOneAns === 0 ? "step-1-card step-1-card-active" : "step-1-card"
            }
            onClick={() => {
              setCurrentStep("STEP_1", "SPEND_NEW_HOME", 0);
            }}
          >
            <div
              className={
                qOneAns === 0
                  ? "circle-default circle-active"
                  : "circle-default"
              }
            ></div>
            <img src="/images/q-steps/start-purchase.png" alt=""></img>
            <p>
              {t("questionsStep.I_want_to")}
              <span
                className={
                  qOneAns === 0
                    ? "p-bold-default p-bold-active"
                    : "p-bold-default"
                }
              >
                {t("questionsStep.buy")}
              </span>{" "}
              {t("questionsStep.a_home")}
            </p>
          </div>
        </div>
        <div className="col-sm-4">
          <div
            className={
              qOneAns === 1 ? "step-1-card step-1-card-active" : "step-1-card"
            }
            onClick={() => {
              setCurrentStep("STEP_1", "LOAN_TYPE", 1);
            }}
          >
            <div
              className={
                qOneAns === 1
                  ? "circle-default circle-active"
                  : "circle-default"
              }
            ></div>
            <img src="/images/q-steps/start-refinance.png" alt=""></img>
            <p>
              {t("questionsStep.I_want_to")}
              <span
                className={
                  qOneAns === 1
                    ? "p-bold-default p-bold-active"
                    : "p-bold-default"
                }
              >
                {t("questionsStep.refinance")}
              </span>{" "}
              {t("questionsStep.my_home")}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default StepOne;
