import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import validateName from "../../../util/NameValidation";

import "./UserName.css";

function UserName({ setCurrentStep, ansUserName }) {
  const [userName, setUserName] = useState(ansUserName);
  const [firstNameError, setFirstNameError] = useState(false);
  const [lastNameError, setLastNameError] = useState(false);

  const { t } = useTranslation();

  const nextBtnClick = () => {
    if (userName.firstName === null) {
      setFirstNameError(t("questionsStep.required"));
    } else if (userName.lastName === null) {
      setLastNameError(t("questionsStep.required"));
    } else {
      if (validateName(userName.firstName)[1]) {
        setFirstNameError(null);

        if (validateName(userName.lastName)[1]) {
          setLastNameError(null);

          setCurrentStep("USER_NAME", "USER_PHONE_NUMBER", userName);
        } else {
          setLastNameError(validateName(userName.lastName)[0]);
        }
      } else {
        setFirstNameError(validateName(userName.firstName)[0]);
      }
    }
  };

  const firstInputOnChange = (value) => {
    setUserName({ firstName: value, lastName: userName?.lastName });

    if (validateName(value)[1]) {
      setFirstNameError(null);
    } else {
      setFirstNameError(validateName(value)[0]);
    }
  };

  const lastInputOnChange = (value) => {
    setUserName({ firstName: userName?.firstName, lastName: value });

    if (validateName(value)[1]) {
      setLastNameError(null);
    } else {
      setLastNameError(validateName(value)[0]);
    }
  };

  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.USER_NAME.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-5 d-flex justify-content-center">
        <div className="col-sm-3">
          <input
            value={userName.firstName ? userName.firstName : ""}
            className="input-box-style"
            placeholder={t("questionsStep.USER_NAME.input_1")}
            onChange={(e) => {
              firstInputOnChange(e.target.value);
            }}
          />
          {firstNameError && (
            <div className="error-message">{t(firstNameError)}</div>
          )}
        </div>
        <div className="col-sm-3">
          <input
            value={userName.lastName ? userName.lastName : ""}
            className="input-box-style"
            placeholder={t("questionsStep.USER_NAME.input_2")}
            onChange={(e) => {
              lastInputOnChange(e.target.value);
            }}
          />
          {lastNameError && (
            <div className="error-message">{t(lastNameError)}</div>
          )}
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            nextBtnClick();
          }}
        >
          <div className="step-q-button">{t("questionsStep.next")}</div>
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("USER_NAME", "USER_EMAIL", ansUserName);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default UserName;
