import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./OwnHome.css";

function OwnHome({ setCurrentStep, ansOwnHome }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.OWN_HOME.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansOwnHome === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("OWN_HOME", "WHEN_PURCHASE", 0);
            }}
          >
            {ansOwnHome === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.OWN_HOME.ans_1")}
          </div>
          <div
            className={
              ansOwnHome === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("OWN_HOME", "WHEN_PURCHASE", 1);
            }}
          >
            {ansOwnHome === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.OWN_HOME.ans_2")}
          </div>
          <div
            className={
              ansOwnHome === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("OWN_HOME", "WHEN_PURCHASE", 2);
            }}
          >
            {ansOwnHome === 2 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.OWN_HOME.ans_3")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("OWN_HOME", "WHEN_PURCHASE", ansOwnHome);
          }}
        >
          {ansOwnHome !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("OWN_HOME", "FIRST_TIME_USE", ansOwnHome);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default OwnHome;
