import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./HaveBankruptcy.css";

function HaveBankruptcy({ setCurrentStep, ansHaveBankruptcy }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.HAVE_BANKRUPTCY.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansHaveBankruptcy === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("HAVE_BANKRUPTCY", "CURRENT_CREDIT_SCORE", 0);
            }}
          >
            {ansHaveBankruptcy === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HAVE_BANKRUPTCY.ans_1")}
          </div>
          <div
            className={
              ansHaveBankruptcy === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("HAVE_BANKRUPTCY", "CURRENT_CREDIT_SCORE", 1);
            }}
          >
            {ansHaveBankruptcy === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HAVE_BANKRUPTCY.ans_2")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "HAVE_BANKRUPTCY",
              "CURRENT_CREDIT_SCORE",
              ansHaveBankruptcy
            );
          }}
        >
          {ansHaveBankruptcy !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "HAVE_BANKRUPTCY",
              "ANNUAL_INCOME",
              ansHaveBankruptcy
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default HaveBankruptcy;
