import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./HomeType.css";

function HomeType({ setCurrentStep, ansHomeType, qOneAns }) {
  const { t } = useTranslation();

  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.HOME_TYPE.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansHomeType === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("HOME_TYPE", "HOW_USE_HOME", 0);
            }}
          >
            {ansHomeType === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HOME_TYPE.ans_1")}
          </div>
          <div
            className={
              ansHomeType === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("HOME_TYPE", "HOW_USE_HOME", 2);
            }}
          >
            {ansHomeType === 2 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HOME_TYPE.ans_2")}
          </div>
          <div
            className={
              ansHomeType === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("HOME_TYPE", "OWN_LAND", 4);
            }}
          >
            {ansHomeType === 4 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HOME_TYPE.ans_3")}
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansHomeType === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("HOME_TYPE", "HOW_USE_HOME", 1);
            }}
          >
            {ansHomeType === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HOME_TYPE.ans_4")}
          </div>
          <div
            className={
              ansHomeType === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("HOME_TYPE", "HOW_USE_HOME", 3);
            }}
          >
            {ansHomeType === 3 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HOME_TYPE.ans_5")}
          </div>
          <div
            className={
              ansHomeType === 5
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("HOME_TYPE", "HOW_USE_HOME", 5);
            }}
          >
            {ansHomeType === 5 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HOME_TYPE.ans_6")}
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansHomeType === 6
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("HOME_TYPE", "HOW_USE_HOME", 6);
            }}
          >
            {ansHomeType === 6 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HOME_TYPE.ans_7")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "HOME_TYPE",
              ansHomeType === 4 ? "OWN_LAND" : "HOW_USE_HOME",
              ansHomeType
            );
          }}
        >
          {ansHomeType !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "HOME_TYPE",
              qOneAns === 0 ? "SPEND_NEW_HOME" : "MORTGAGE_INTEREST",
              ansHomeType
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default HomeType;
