import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import validateEmail from "../../../util/EmailValidation";

import "./UserEmail.css";

function UserEmail({ setCurrentStep, ansUserEmail, qOneAns }) {
  const [emailValue, setEmailValue] = useState(ansUserEmail);
  const [haveError, setHaveError] = useState(false);

  const { t } = useTranslation();

  const nextBtnClick = () => {
    if (emailValue === null) {
      setHaveError(true);
    } else {
      if (validateEmail(emailValue)) {
        setHaveError(false);
        setCurrentStep("USER_EMAIL", "USER_NAME", emailValue);
      } else {
        setHaveError(true);
      }
    }
  };

  const inputOnChange = (value) => {
    setEmailValue(value);
    if (validateEmail(value)) {
      setHaveError(false);
    } else {
      setHaveError(true);
    }
  };

  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.USER_EMAIL.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-5 d-flex justify-content-center">
        <div className="col-sm-4">
          <input
            value={emailValue ? emailValue : ""}
            className="input-box-style"
            placeholder={t("questionsStep.USER_EMAIL.placeHolder")}
            onChange={(e) => {
              inputOnChange(e.target.value);
            }}
          />
          {haveError && (
            <div className="error-message">
              {t("questionsStep.USER_EMAIL.error")}
            </div>
          )}
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            nextBtnClick();
          }}
        >
          <div className="step-q-button">{t("questionsStep.next")}</div>
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "USER_EMAIL",
              qOneAns === 0 ? "CURRENT_ZIP_CODE" : "CURRENT_ADDRESS",
              ansUserEmail
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default UserEmail;
