import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./LoanGoal.css";

function LoanGoal({ setCurrentStep, ansLoanGoal }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.LOAN_GOAL.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansLoanGoal === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_GOAL", "HOME_VALUE", 0);
            }}
          >
            {ansLoanGoal === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.LOAN_GOAL.ans_1")}
          </div>
          <div
            className={
              ansLoanGoal === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_GOAL", "HOME_VALUE", 1);
            }}
          >
            {ansLoanGoal === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.LOAN_GOAL.ans_2")}
          </div>
          <div
            className={
              ansLoanGoal === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_GOAL", "HOME_VALUE", 2);
            }}
          >
            {ansLoanGoal === 2 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.LOAN_GOAL.ans_3")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("LOAN_GOAL", "HOME_VALUE", ansLoanGoal);
          }}
        >
          {ansLoanGoal !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("LOAN_GOAL", "LOAN_TYPE", ansLoanGoal);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default LoanGoal;
