import axios from "axios";

export const bitrixLeadCreate = async ({
  qOneAns,
  ansLoanType,
  ansLoanGoal,
  ansHomeValue,
  ansRemainingBalance,
  ansMortgageInterest,
  ansSpendNewHome,
  ansHomeType,
  ansHowUseHome,
  ansFirstTimeUse,
  ansWhenPurchase,
  ansServedMilitary,
  ansDownPayment,
  ansCurrentEmployment,
  ansAnnualIncome,
  ansHaveBankruptcy,
  ansCurrentCreditScore,
  ansWorkingRealEstate,
  ansWhereBuy,
  ansCurrentZipCode,
  ansCurrentStreetAddress,
  ansUserEmail,
  firstName,
  lastName,
  ansUserPhoneNumber,
  ansMilitaryBranch,
  ansOwnLand,
  ansMortgagePayment,
  ansOwnHome,
}) => {
  try {
    let formData = new FormData();

    formData.append("qOneAns", qOneAns);
    formData.append("ansLoanType", ansLoanType);
    formData.append("ansLoanGoal", ansLoanGoal);
    formData.append("ansHomeValue", ansHomeValue);
    formData.append("ansRemainingBalance", ansRemainingBalance);
    formData.append("ansMortgageInterest", ansMortgageInterest);
    formData.append("ansSpendNewHome", ansSpendNewHome);
    formData.append("ansHomeType", ansHomeType);
    formData.append("ansHowUseHome", ansHowUseHome);
    formData.append("ansFirstTimeUse", ansFirstTimeUse);
    formData.append("ansOwnHome", ansOwnHome);
    formData.append("ansWhenPurchase", ansWhenPurchase);
    formData.append("ansServedMilitary", ansServedMilitary);
    formData.append("ansDownPayment", ansDownPayment);
    formData.append("ansCurrentEmployment", ansCurrentEmployment);
    formData.append("ansAnnualIncome", ansAnnualIncome);
    formData.append("ansHaveBankruptcy", ansHaveBankruptcy);
    formData.append("ansCurrentCreditScore", ansCurrentCreditScore);
    formData.append("ansWorkingRealEstate", ansWorkingRealEstate);
    formData.append("ansWhereBuy", ansWhereBuy);
    formData.append("ansCurrentZipCode", ansCurrentZipCode);
    formData.append("ansCurrentStreetAddress", ansCurrentStreetAddress);
    formData.append("ansUserEmail", ansUserEmail);
    formData.append("firstName", firstName);
    formData.append("lastName", lastName);
    formData.append("ansUserPhoneNumber", ansUserPhoneNumber);
    formData.append("ansMilitaryBranch", ansMilitaryBranch);
    formData.append("ansOwnLand", ansOwnLand);
    formData.append("ansMortgagePayment", ansMortgagePayment);
    formData.append("sourceTitle", "COMOCOMPRARSUCASA");
    formData.append("sourceId", "36");
    formData.append("sourceUrl", "https://autorizado.com/");

    const { data } = await axios({
      method: "post",
      url: `${process.env.REACT_APP_API_HOST}/api/bitrix/create_lead.php`,
      data: formData,
      headers: {
        Accept: "application/json",
        "Content-Type": `multipart/form-data; boundary=${formData._boundary}`,
      },
    });

    return data;
  } catch (error) {
    if (error.response) {
      return error.response.data; // => the response payload
    } else {
      //console.log(error);
    }
  }
};
