import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import ZipCodeBox from "../../../components/ZipCodeBox/ZipCodeBox";

import "./CurrentZipCode.css";

function CurrentZipCode({ setCurrentStep, ansCurrentZipCode }) {
  const [selectedLocation, setSelectedLocation] = useState(ansCurrentZipCode);
  const [haveError, setHaveError] = useState(false);

  const { t } = useTranslation();

  const nextBtnClick = () => {
    if (selectedLocation !== null) {
      setCurrentStep("CURRENT_ZIP_CODE", "USER_EMAIL", selectedLocation);
      setHaveError(false);
    } else {
      setHaveError(true);
    }
  };

  const changeCurrentValue = (value) => {
    setSelectedLocation(value);
    setCurrentStep("CURRENT_ZIP_CODE", "USER_EMAIL", value);
  };

  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.CURRENT_ZIP_CODE.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-5 d-flex justify-content-center">
        <div className="col-sm-4">
          <ZipCodeBox
            changeCurrentValue={changeCurrentValue}
            selectedOption={ansCurrentZipCode}
          />
          {haveError && (
            <div className="error-message">
              {t("questionsStep.CURRENT_ZIP_CODE.error")}
            </div>
          )}
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            nextBtnClick();
          }}
        >
          <div className="step-q-button">{t("questionsStep.next")}</div>
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("CURRENT_ZIP_CODE", "WHERE_BUY", ansCurrentZipCode);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default CurrentZipCode;
