const validateStreetAddress = (streetAddress) => {
  if (
    streetAddress === null ||
    streetAddress === "" ||
    typeof streetAddress === "undefined"
  ) {
    return ["questionsStep.CURRENT_ADDRESS.Can_not_empty", false];
  } else if (streetAddress.length > 20) {
    return ["questionsStep.CURRENT_ADDRESS.Should_less_30", false];
  } else {
    return ["Success", true];
  }
};

export default validateStreetAddress;
