import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./SpendNewHome.css";

function SpendNewHome({ setCurrentStep, ansSpendNewHome }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.SPEND_NEW_HOME.head")}
          </h2>
          <h3 className="q-step-sub-text">
            {t("questionsStep.SPEND_NEW_HOME.sub")}
          </h3>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansSpendNewHome === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 0);
            }}
          >
            {ansSpendNewHome === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SPEND_NEW_HOME.ans_1")}
          </div>
          <div
            className={
              ansSpendNewHome === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 2);
            }}
          >
            {ansSpendNewHome === 2 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SPEND_NEW_HOME.ans_3")}
          </div>
          <div
            className={
              ansSpendNewHome === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 4);
            }}
          >
            {ansSpendNewHome === 4 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SPEND_NEW_HOME.ans_5")}
          </div>
          <div
            className={
              ansSpendNewHome === 6
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 6);
            }}
          >
            {ansSpendNewHome === 6 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SPEND_NEW_HOME.ans_7")}
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansSpendNewHome === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 1);
            }}
          >
            {ansSpendNewHome === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SPEND_NEW_HOME.ans_2")}
          </div>
          <div
            className={
              ansSpendNewHome === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 3);
            }}
          >
            {ansSpendNewHome === 3 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SPEND_NEW_HOME.ans_4")}
          </div>
          <div
            className={
              ansSpendNewHome === 5
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 5);
            }}
          >
            {ansSpendNewHome === 5 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SPEND_NEW_HOME.ans_6")}
          </div>
          <div
            className={
              ansSpendNewHome === 7
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 7);
            }}
          >
            {ansSpendNewHome === 7 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SPEND_NEW_HOME.ans_8")}
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansSpendNewHome === 8
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", 8);
            }}
          >
            {ansSpendNewHome === 8 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.SPEND_NEW_HOME.ans_9")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("SPEND_NEW_HOME", "HOME_TYPE", ansSpendNewHome);
          }}
        >
          {ansSpendNewHome !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("SPEND_NEW_HOME", "STEP_1", ansSpendNewHome);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default SpendNewHome;
