import axios from "axios";

export const getFilteredZipCodeCityList = async (word) => {
  try {
    let formData = new FormData();

    formData.append("q", word);

    const { data } = await axios({
      method: "post",
      url: `${process.env.REACT_APP_API_HOST}/api/zip_code/filter_by_zip_code_and_city.php`,
      data: formData,
      headers: {
        Accept: "application/json",
        "Content-Type": `multipart/form-data; boundary=${formData._boundary}`,
      },
    });

    return data;
  } catch (error) {
    if (error.response) {
      return error.response.data; // => the response payload
    } else {
      //console.log(error);
    }
  }
};
