import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./FirstTimePurchasing.css";

function FirstTimePurchasing({ setCurrentStep, ansFirstTimeUse }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.FIRST_TIME_USE.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansFirstTimeUse === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("FIRST_TIME_USE", "OWN_HOME", 0);
            }}
          >
            {ansFirstTimeUse === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.FIRST_TIME_USE.ans_1")}
          </div>
          <div
            className={
              ansFirstTimeUse === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("FIRST_TIME_USE", "WHEN_PURCHASE", 1);
            }}
          >
            {ansFirstTimeUse === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.FIRST_TIME_USE.ans_2")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "FIRST_TIME_USE",
              ansFirstTimeUse === 0 ? "OWN_HOME" : "WHEN_PURCHASE",
              ansFirstTimeUse
            );
          }}
        >
          {ansFirstTimeUse !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("FIRST_TIME_USE", "HOW_USE_HOME", ansFirstTimeUse);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default FirstTimePurchasing;
