import { useTranslation } from "react-i18next";
import "./LoadingDetails.css";

function LoadingDetails({ ansUserName }) {
  const { t } = useTranslation();

  return (
    <div>
      <div className="row d-flex justify-content-center hero-text mt-5 mb-5">
        <div className="col-sm-7 loading-step">
          <img
            className="logo-loading"
            src="/images/logo.webp"
            alt="logo"
          ></img>
          <h2 className="">
            {t("questionsStep.LOADING_DETAILS.head")} {ansUserName?.firstName}!
          </h2>
          <h5 className="">{t("questionsStep.LOADING_DETAILS.sub")}</h5>

          <div className="loading-dots-step">
            <div className="dot" />
            <div className="dot" />
            <div className="dot" />
            <div className="dot" />
            <div className="dot" />
          </div>
        </div>
      </div>
    </div>
  );
}

export default LoadingDetails;
