import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./CurrentEmployment.css";

function CurrentEmployment({
  setCurrentStep,
  ansCurrentEmployment,
  qOneAns,
  ansServedMilitary,
}) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.CURRENT_EMPLOYMENT.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansCurrentEmployment === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("CURRENT_EMPLOYMENT", "ANNUAL_INCOME", 0);
            }}
          >
            {ansCurrentEmployment === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.CURRENT_EMPLOYMENT.ans_1")}
          </div>
          <div
            className={
              ansCurrentEmployment === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("CURRENT_EMPLOYMENT", "ANNUAL_INCOME", 1);
            }}
          >
            {ansCurrentEmployment === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.CURRENT_EMPLOYMENT.ans_2")}
          </div>
          <div
            className={
              ansCurrentEmployment === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("CURRENT_EMPLOYMENT", "ANNUAL_INCOME", 2);
            }}
          >
            {ansCurrentEmployment === 2 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.CURRENT_EMPLOYMENT.ans_3")}
          </div>
          <div
            className={
              ansCurrentEmployment === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("CURRENT_EMPLOYMENT", "ANNUAL_INCOME", 3);
            }}
          >
            {ansCurrentEmployment === 3 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.CURRENT_EMPLOYMENT.ans_4")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "CURRENT_EMPLOYMENT",
              "ANNUAL_INCOME",
              ansCurrentEmployment
            );
          }}
        >
          {ansCurrentEmployment !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "CURRENT_EMPLOYMENT",
              qOneAns === 0
                ? "DOWN_PAYMENT"
                : ansServedMilitary === 0
                ? "SERVED_MILITARY"
                : "MILITARY_BRANCH",
              ansCurrentEmployment
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default CurrentEmployment;
