import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./WhenPurchase.css";

function WhenPurchase({ setCurrentStep, ansWhenPurchase, ansFirstTimeUse }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.WHEN_PURCHASE.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansWhenPurchase === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 0);
            }}
          >
            {ansWhenPurchase === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.WHEN_PURCHASE.ans_1")}
          </div>
          <div
            className={
              ansWhenPurchase === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 2);
            }}
          >
            {ansWhenPurchase === 2 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.WHEN_PURCHASE.ans_2")}
          </div>
          <div
            className={
              ansWhenPurchase === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 4);
            }}
          >
            {ansWhenPurchase === 4 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.WHEN_PURCHASE.ans_3")}
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansWhenPurchase === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 1);
            }}
          >
            {ansWhenPurchase === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.WHEN_PURCHASE.ans_4")}
          </div>
          <div
            className={
              ansWhenPurchase === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 3);
            }}
          >
            {ansWhenPurchase === 3 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.WHEN_PURCHASE.ans_5")}
          </div>
          <div
            className={
              ansWhenPurchase === 5
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 5);
            }}
          >
            {ansWhenPurchase === 5 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.WHEN_PURCHASE.ans_6")}
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansWhenPurchase === 6
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", 6);
            }}
          >
            {ansWhenPurchase === 6 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.WHEN_PURCHASE.ans_7")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("WHEN_PURCHASE", "SERVED_MILITARY", ansWhenPurchase);
          }}
        >
          {ansWhenPurchase !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "WHEN_PURCHASE",
              ansFirstTimeUse === 0 ? "OWN_HOME" : "FIRST_TIME_USE",
              ansWhenPurchase
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default WhenPurchase;
