function CityZipCodeBox({ selectedOption, changeCurrentValue }) {
  const [selectedValue, setSelectedValue] = useState(selectedOption);

  const { t } = useTranslation();

  const handleChange = (value) => {
    if (value !== null) {
      setSelectedValue(value);
      changeCurrentValue(value);
    }
  };

  const loadOptions = (input, callback) => {
    getFilteredZipCodeCityList(input).then((data) => {
      try {
        if (data["result"]) {
          if (data["result"] === "SUCCESS") {
            callback([
              {
                label: t("questionsStep.Cities_and_Zip_Code"),
                options: data["data"],
              },
            ]);
          } else if (data["result"] === "FAILED") {
            callback([]);
          } else {
            callback([]);
          }
        } else {
          callback([]);
        }
      } catch (error) {
        callback([]);
      }
    });
  };

  return (
    <Async
      className="step-input-box"
      loadOptions={loadOptions}
      value={selectedValue}
      placeholder={t("questionsStep.City_or_Zip_Code")}
      formatGroupLabel={formatGroupLabel}
      onChange={(value) => {
        handleChange(value);
      }}
      cacheOptions={true}
      grouped
    />
  );
}

export default CityZipCodeBox;
