import React, { useState } from "react";
import { useTranslation } from "react-i18next";

import CurrencyInput from "../../../components/CurrencyInput/CurrencyInput";

import "./HomeValue.css";

function HomeValue({ setCurrentStep, ansHomeValue }) {
  const [homeValue, setHomeValue] = useState(ansHomeValue);
  const [haveError, setHaveError] = useState(false);

  const { t } = useTranslation();

  const nextBtnClick = () => {
    if (homeValue === "") {
      setHaveError(true);
    } else {
      setCurrentStep("HOME_VALUE", "REMAINING_BALANCE", homeValue);
    }
  };

  const inputOnChange = (value) => {
    setHomeValue(value);

    if (value !== "") {
      setHaveError(false);
    } else {
      setHaveError(true);
    }
  };

  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.HOME_VALUE.head")}
          </h2>
          <h3 className="q-step-sub-text">
            {t("questionsStep.HOME_VALUE.sub")}
          </h3>
        </div>
      </div>
      <div className="row mt-5 d-flex justify-content-center">
        <div className="col-sm-3">
          <div className="input-box-content">
            <div className="input-box-style-symbl">
              <CurrencyInput
                className="input-box-style"
                value={homeValue ? homeValue : ""}
                type="text"
                onChange={(e) => {
                  inputOnChange(e.target.value);
                }}
              />
            </div>
          </div>
          {haveError && (
            <div className="error-message">
              {t("questionsStep.HOME_VALUE.error")}
            </div>
          )}
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            nextBtnClick();
          }}
        >
          <div className="step-q-button">{t("questionsStep.next")}</div>
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("HOME_VALUE", "LOAN_GOAL", ansHomeValue);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default HomeValue;
