import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./WorkingRealEstate.css";

function WorkingRealEstate({ setCurrentStep, ansWorkingRealEstate }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.WORK_REAL_ESTATE.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansWorkingRealEstate === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WORK_REAL_ESTATE", "WHERE_BUY", 0);
            }}
          >
            {ansWorkingRealEstate === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.WORK_REAL_ESTATE.ans_1")}
          </div>
          <div
            className={
              ansWorkingRealEstate === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("WORK_REAL_ESTATE", "WHERE_BUY", 1);
            }}
          >
            {ansWorkingRealEstate === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.WORK_REAL_ESTATE.ans_2")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "WORK_REAL_ESTATE",
              "WHERE_BUY",
              ansWorkingRealEstate
            );
          }}
        >
          {ansWorkingRealEstate !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "WORK_REAL_ESTATE",
              "CURRENT_CREDIT_SCORE",
              ansWorkingRealEstate
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default WorkingRealEstate;
