import React, { useState } from "react";
import { CSSTransition, TransitionGroup } from "react-transition-group";

import StepOne from "./StepOne/StepOne";
import SpendNewHome from "./SpendNewHome/SpendNewHome";
import HomeType from "./HomeType/HomeType";
import HowUseHome from "./HowUseHome/HowUseHome";
import FirstTimePurchasing from "./FirstTimePurchasing/FirstTimePurchasing";
import OwnHome from "./OwnHome/OwnHome";
import WhenPurchase from "./WhenPurchase/WhenPurchase";
import ServedMilitary from "./ServedMilitary/ServedMilitary";
import DownPayment from "./DownPayment/DownPayment";
import CurrentEmployment from "./CurrentEmployment/CurrentEmployment";
import AnnualIncome from "./AnnualIncome/AnnualIncome";
import HaveBankruptcy from "./HaveBankruptcy/HaveBankruptcy";
import CurrentCreditScore from "./CurrentCreditScore/CurrentCreditScore";
import WorkingRealEstate from "./WorkingRealEstate/WorkingRealEstate";
import WhereBuy from "./WhereBuy/WhereBuy";
import CurrentZipCode from "./CurrentZipCode/CurrentZipCode";
import UserEmail from "./UserEmail/UserEmail";
import UserName from "./UserName/UserName";
import UserPhoneNumber from "./UserPhoneNumber/UserPhoneNumber";
import MilitaryBranch from "./MilitaryBranch/MilitaryBranch";
import LoanType from "./LoanType/LoanType";
import LoanGoal from "./LoanGoal/LoanGoal";
import HomeValue from "./HomeValue/HomeValue";
import RemainingBalance from "./RemainingBalance/RemainingBalance";
import MortgageInterest from "./MortgageInterest/MortgageInterest";
import CurrentAddress from "./CurrentAddress/CurrentAddress";
import OwnLand from "./OwnLand/OwnLand";
import MortgagePayment from "./MortgagePayment/MortgagePayment";
import ThankYou from "./ThankYou/ThankYou";
import {
  ansAnnualIncomeList,
  ansCurrentCreditScoreList,
  ansCurrentEmploymentList,
  ansDownPaymentList,
  ansFirstTimeUseList,
  ansHaveBankruptcyList,
  ansHomeTypeList,
  ansHowUseHomeList,
  ansLoanGoalList,
  ansLoanTypeList,
  ansMilitaryBranchList,
  ansMortgagePaymentList,
  ansOwnHomeList,
  ansOwnLandList,
  ansServedMilitaryList,
  ansSpendNewHomeList,
  ansWhenPurchaseList,
  ansWorkingRealEstateList,
  qOneAnsList,
} from "../../Data/QuestionAnswer";
import { bitrixLeadCreate } from "../../api/leadAPI";

import "./QuoteSteps.css";
import FailSubmission from "./FailSubmission/FailSubmission";
import LoadingDetails from "./LoadingDetails/LoadingDetails";

function QuoteSteps({
  quoteType = "light",
  stepOneTitle = "",
  stepOneSub = "",
}) {
  const [currentStep, setCurrentStep] = useState("STEP_1");
  const [qOneAns, setQOneAns] = useState(null);
  const [ansLoanType, setAnsLoanType] = useState(null);
  const [ansLoanGoal, setAnsLoanGoal] = useState(null);
  const [ansHomeValue, setAnsHomeValue] = useState("200,000");
  const [ansRemainingBalance, setAnsRemainingBalance] = useState("150,000");
  const [ansMortgageInterest, setAnsMortgageInterest] = useState(null);
  const [ansSpendNewHome, setAnsSpendNewHome] = useState(null);
  const [ansHomeType, setAnsHomeType] = useState(null);
  const [ansHowUseHome, setAnsHowUseHome] = useState(null);
  const [ansFirstTimeUse, setAnsFirstTimeUse] = useState(null);
  const [ansOwnHome, setAnsOwnHome] = useState(null);
  const [ansWhenPurchase, setAnsWhenPurchase] = useState(null);
  const [ansServedMilitary, setAnsServedMilitary] = useState(null);
  const [ansDownPayment, setAnsDownPayment] = useState(null);
  const [ansCurrentEmployment, setAnsCurrentEmployment] = useState(null);
  const [ansAnnualIncome, setAnsAnnualIncome] = useState(null);
  const [ansHaveBankruptcy, setAnsHaveBankruptcy] = useState(null);
  const [ansCurrentCreditScore, setAnsCurrentCreditScore] = useState(null);
  const [ansWorkingRealEstate, setAnsWorkingRealEstate] = useState(null);
  const [ansWhereBuy, setAnsWhereBuy] = useState(null);
  const [ansCurrentZipCode, setAnsCurrentZipCode] = useState(null);
  const [ansCurrentStreetAddress, setAnsCurrentStreetAddress] = useState(null);
  const [ansUserEmail, setAnsUserEmail] = useState(null);
  const [ansUserName, setAnsUserName] = useState({
    firstName: null,
    lastName: null,
  });
  const [ansUserPhoneNumber, setAnsUserPhoneNumber] = useState(null);
  const [ansMilitaryBranch, setAnsMilitaryBranch] = useState(null);
  const [ansOwnLand, setAnsOwnLand] = useState(null);
  const [ansMortgagePayment, setAnsMortgagePayment] = useState(null);

  const changeStep = (lastStep, currentStep, value) => {
    switch (lastStep) {
      case "STEP_1":
        setCurrentStep(currentStep);
        setQOneAns(value);
        break;
      case "LOAN_TYPE":
        setCurrentStep(currentStep);
        setAnsLoanType(value);
        break;
      case "LOAN_GOAL":
        setCurrentStep(currentStep);
        setAnsLoanGoal(value);
        break;
      case "HOME_VALUE":
        setCurrentStep(currentStep);
        setAnsHomeValue(value);
        break;
      case "REMAINING_BALANCE":
        setCurrentStep(currentStep);
        setAnsRemainingBalance(value);
        break;
      case "MORTGAGE_INTEREST":
        setCurrentStep(currentStep);
        setAnsMortgageInterest(value);
        break;
      case "MORTGAGE_PAYMENT":
        setCurrentStep(currentStep);
        setAnsMortgagePayment(value);
        break;
      case "SPEND_NEW_HOME":
        setCurrentStep(currentStep);
        setAnsSpendNewHome(value);
        break;
      case "HOME_TYPE":
        setCurrentStep(currentStep);
        setAnsHomeType(value);
        break;
      case "HOW_USE_HOME":
        setCurrentStep(currentStep);
        setAnsHowUseHome(value);
        break;
      case "FIRST_TIME_USE":
        setCurrentStep(currentStep);
        setAnsFirstTimeUse(value);
        break;
      case "OWN_HOME":
        setCurrentStep(currentStep);
        setAnsOwnHome(value);
        break;
      case "OWN_LAND":
        setCurrentStep(currentStep);
        setAnsOwnLand(value);
        break;
      case "WHEN_PURCHASE":
        setCurrentStep(currentStep);
        setAnsWhenPurchase(value);
        break;
      case "SERVED_MILITARY":
        setCurrentStep(currentStep);
        setAnsServedMilitary(value);
        break;
      case "MILITARY_BRANCH":
        setCurrentStep(currentStep);
        setAnsMilitaryBranch(value);
        break;
      case "DOWN_PAYMENT":
        setCurrentStep(currentStep);
        setAnsDownPayment(value);
        break;
      case "CURRENT_EMPLOYMENT":
        setCurrentStep(currentStep);
        setAnsCurrentEmployment(value);
        break;
      case "ANNUAL_INCOME":
        setCurrentStep(currentStep);
        setAnsAnnualIncome(value);
        break;
      case "HAVE_BANKRUPTCY":
        setCurrentStep(currentStep);
        setAnsHaveBankruptcy(value);
        break;
      case "CURRENT_CREDIT_SCORE":
        setCurrentStep(currentStep);
        setAnsCurrentCreditScore(value);
        break;
      case "WORK_REAL_ESTATE":
        setCurrentStep(currentStep);
        setAnsWorkingRealEstate(value);
        break;
      case "WHERE_BUY":
        setCurrentStep(currentStep);
        setAnsWhereBuy(value);
        break;
      case "CURRENT_ZIP_CODE":
        setCurrentStep(currentStep);
        setAnsCurrentZipCode(value);
        break;
      case "CURRENT_ADDRESS":
        setCurrentStep(currentStep);
        setAnsCurrentZipCode(value?.zipCode);
        setAnsCurrentStreetAddress(value?.streetAddress);
        break;
      case "USER_EMAIL":
        setCurrentStep(currentStep);
        setAnsUserEmail(value);
        break;
      case "USER_NAME":
        setCurrentStep(currentStep);
        setAnsUserName(value);
        break;
      case "USER_PHONE_NUMBER":
        setAnsUserPhoneNumber(value);
        setCurrentStep("LOADING_DETAILS");

        const leadDetails = {
          qOneAns: qOneAns !== null && qOneAnsList[qOneAns].value,
          ansLoanType:
            ansLoanType !== null && ansLoanTypeList[ansLoanType].value,
          ansLoanGoal:
            ansLoanGoal !== null && ansLoanGoalList[ansLoanGoal].value,
          ansHomeValue: ansHomeValue,
          ansRemainingBalance: ansRemainingBalance,
          ansMortgageInterest: ansMortgageInterest,
          ansSpendNewHome:
            ansSpendNewHome !== null &&
            ansSpendNewHomeList[ansSpendNewHome].value,
          ansHomeType:
            ansHomeType !== null && ansHomeTypeList[ansHomeType].value,
          ansHowUseHome:
            ansHowUseHome !== null && ansHowUseHomeList[ansHowUseHome].value,
          ansFirstTimeUse:
            ansFirstTimeUse !== null &&
            ansFirstTimeUseList[ansFirstTimeUse].value,
          ansOwnHome: ansOwnHome !== null && ansOwnHomeList[ansOwnHome].value,
          ansWhenPurchase:
            ansWhenPurchase !== null &&
            ansWhenPurchaseList[ansWhenPurchase].value,
          ansServedMilitary:
            ansServedMilitary !== null &&
            ansServedMilitaryList[ansServedMilitary].value,
          ansDownPayment:
            ansDownPayment !== null && ansDownPaymentList[ansDownPayment].value,
          ansCurrentEmployment:
            ansCurrentEmployment !== null &&
            ansCurrentEmploymentList[ansCurrentEmployment].value,
          ansAnnualIncome:
            ansAnnualIncome !== null &&
            ansAnnualIncomeList[ansAnnualIncome].value,
          ansHaveBankruptcy:
            ansHaveBankruptcy !== null &&
            ansHaveBankruptcyList[ansHaveBankruptcy].value,
          ansCurrentCreditScore:
            ansCurrentCreditScore !== null &&
            ansCurrentCreditScoreList[ansCurrentCreditScore].value,
          ansWorkingRealEstate:
            ansWorkingRealEstate !== null &&
            ansWorkingRealEstateList[ansWorkingRealEstate].value,
          ansWhereBuy: ansWhereBuy !== null && ansWhereBuy.value,
          ansCurrentZipCode:
            ansCurrentZipCode !== null && ansCurrentZipCode.value,
          ansCurrentStreetAddress: ansCurrentStreetAddress,
          ansUserEmail: ansUserEmail,
          firstName: ansUserName !== null && ansUserName.firstName,
          lastName: ansUserName !== null && ansUserName.lastName,
          ansUserPhoneNumber: value,
          ansMilitaryBranch:
            ansMilitaryBranch !== null &&
            ansMilitaryBranchList[ansMilitaryBranch].value,
          ansOwnLand: ansOwnLand !== null && ansOwnLandList[ansOwnLand].value,
          ansMortgagePayment:
            ansMortgagePayment !== null &&
            ansMortgagePaymentList[ansMortgagePayment].value,
        };
        console.log(leadDetails);

        bitrixLeadCreate(leadDetails).then((data) => {
          try {
            if (data["result"]) {
              if (data["result"] === "SUCCESS") {
                setCurrentStep(currentStep);
              } else if (data["result"] === "FAILED") {
                setCurrentStep("FAIL_SUBMISSION");
              } else {
                setCurrentStep("FAIL_SUBMISSION");
              }
            } else {
              setCurrentStep("FAIL_SUBMISSION");
            }
          } catch (error) {
            setCurrentStep("FAIL_SUBMISSION");
          }
        });

        break;
      default:
        return <h1>default</h1>;
    }
  };

  const renderSteps = () => {
    switch (currentStep) {
      case "STEP_1":
        return (
          <CSSTransition
            key={1}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <StepOne
              setCurrentStep={changeStep}
              qOneAns={qOneAns}
              stepOneTitle={stepOneTitle}
              stepOneSub={stepOneSub}
            />
          </CSSTransition>
        );
      case "SPEND_NEW_HOME":
        return (
          <CSSTransition
            key={2}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <SpendNewHome
              setCurrentStep={changeStep}
              ansSpendNewHome={ansSpendNewHome}
            />
          </CSSTransition>
        );
      case "LOAN_TYPE":
        return (
          <CSSTransition
            key={3}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <LoanType setCurrentStep={changeStep} ansLoanType={ansLoanType} />
          </CSSTransition>
        );
      case "LOAN_GOAL":
        return (
          <CSSTransition
            key={4}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <LoanGoal setCurrentStep={changeStep} ansLoanGoal={ansLoanGoal} />
          </CSSTransition>
        );
      case "HOME_VALUE":
        return (
          <CSSTransition
            key={5}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <HomeValue
              setCurrentStep={changeStep}
              ansHomeValue={ansHomeValue}
            />
          </CSSTransition>
        );
      case "REMAINING_BALANCE":
        return (
          <CSSTransition
            key={6}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <RemainingBalance
              setCurrentStep={changeStep}
              ansRemainingBalance={ansRemainingBalance}
            />
          </CSSTransition>
        );
      case "MORTGAGE_INTEREST":
        return (
          <CSSTransition
            key={7}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <MortgageInterest
              setCurrentStep={changeStep}
              ansMortgageInterest={ansMortgageInterest}
            />
          </CSSTransition>
        );
      case "HOME_TYPE":
        return (
          <CSSTransition
            key={8}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <HomeType
              setCurrentStep={changeStep}
              ansHomeType={ansHomeType}
              qOneAns={qOneAns}
            />
          </CSSTransition>
        );
      case "HOW_USE_HOME":
        return (
          <CSSTransition
            key={9}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <HowUseHome
              setCurrentStep={changeStep}
              ansHowUseHome={ansHowUseHome}
              qOneAns={qOneAns}
              ansHomeType={ansHomeType}
            />
          </CSSTransition>
        );
      case "OWN_LAND":
        return (
          <CSSTransition
            key={10}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <OwnLand setCurrentStep={changeStep} ansOwnLand={ansOwnLand} />
          </CSSTransition>
        );
      case "FIRST_TIME_USE":
        return (
          <CSSTransition
            key={11}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <FirstTimePurchasing
              setCurrentStep={changeStep}
              ansFirstTimeUse={ansFirstTimeUse}
            />
          </CSSTransition>
        );
      case "OWN_HOME":
        return (
          <CSSTransition
            key={12}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <OwnHome setCurrentStep={changeStep} ansOwnHome={ansOwnHome} />
          </CSSTransition>
        );
      case "WHEN_PURCHASE":
        return (
          <CSSTransition
            key={13}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <WhenPurchase
              setCurrentStep={changeStep}
              ansWhenPurchase={ansWhenPurchase}
              ansFirstTimeUse={ansFirstTimeUse}
            />
          </CSSTransition>
        );
      case "SERVED_MILITARY":
        return (
          <CSSTransition
            key={14}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <ServedMilitary
              setCurrentStep={changeStep}
              ansServedMilitary={ansServedMilitary}
              qOneAns={qOneAns}
            />
          </CSSTransition>
        );
      case "MILITARY_BRANCH":
        return (
          <CSSTransition
            key={15}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <MilitaryBranch
              setCurrentStep={changeStep}
              ansMilitaryBranch={ansMilitaryBranch}
              qOneAns={qOneAns}
            />
          </CSSTransition>
        );
      case "DOWN_PAYMENT":
        return (
          <CSSTransition
            key={16}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <DownPayment
              setCurrentStep={changeStep}
              ansDownPayment={ansDownPayment}
            />
          </CSSTransition>
        );
      case "CURRENT_EMPLOYMENT":
        return (
          <CSSTransition
            key={17}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <CurrentEmployment
              setCurrentStep={changeStep}
              ansCurrentEmployment={ansCurrentEmployment}
              ansServedMilitary={ansServedMilitary}
              qOneAns={qOneAns}
            />
          </CSSTransition>
        );
      case "ANNUAL_INCOME":
        return (
          <CSSTransition
            key={18}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <AnnualIncome
              setCurrentStep={changeStep}
              ansAnnualIncome={ansAnnualIncome}
            />
          </CSSTransition>
        );
      case "HAVE_BANKRUPTCY":
        return (
          <CSSTransition
            key={19}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <HaveBankruptcy
              setCurrentStep={changeStep}
              ansHaveBankruptcy={ansHaveBankruptcy}
            />
          </CSSTransition>
        );
      case "CURRENT_CREDIT_SCORE":
        return (
          <CSSTransition
            key={20}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <CurrentCreditScore
              setCurrentStep={changeStep}
              ansCurrentCreditScore={ansCurrentCreditScore}
              qOneAns={qOneAns}
            />
          </CSSTransition>
        );
      case "WORK_REAL_ESTATE":
        return (
          <CSSTransition
            key={21}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <WorkingRealEstate
              setCurrentStep={changeStep}
              ansWorkingRealEstate={ansWorkingRealEstate}
            />
          </CSSTransition>
        );
      case "MORTGAGE_PAYMENT":
        return (
          <CSSTransition
            key={22}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <MortgagePayment
              setCurrentStep={changeStep}
              ansMortgagePayment={ansMortgagePayment}
            />
          </CSSTransition>
        );
      case "WHERE_BUY":
        return (
          <CSSTransition
            key={23}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <WhereBuy setCurrentStep={changeStep} ansWhereBuy={ansWhereBuy} />
          </CSSTransition>
        );
      case "CURRENT_ZIP_CODE":
        return (
          <CSSTransition
            key={24}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <CurrentZipCode
              setCurrentStep={changeStep}
              ansCurrentZipCode={ansCurrentZipCode}
            />
          </CSSTransition>
        );
      case "CURRENT_ADDRESS":
        return (
          <CSSTransition
            key={25}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <CurrentAddress
              setCurrentStep={changeStep}
              ansCurrentZipCode={ansCurrentZipCode}
              ansCurrentStreetAddress={ansCurrentStreetAddress}
            />
          </CSSTransition>
        );
      case "USER_EMAIL":
        return (
          <CSSTransition
            key={26}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <UserEmail
              setCurrentStep={changeStep}
              ansUserEmail={ansUserEmail}
              qOneAns={qOneAns}
            />
          </CSSTransition>
        );
      case "USER_NAME":
        return (
          <CSSTransition
            key={27}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <UserName setCurrentStep={changeStep} ansUserName={ansUserName} />
          </CSSTransition>
        );
      case "USER_PHONE_NUMBER":
        return (
          <CSSTransition
            key={28}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <UserPhoneNumber
              setCurrentStep={changeStep}
              ansUserPhoneNumber={ansUserPhoneNumber}
            />
          </CSSTransition>
        );
      case "THANK_YOU":
        return (
          <CSSTransition
            key={29}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <ThankYou ansUserName={ansUserName} />
          </CSSTransition>
        );
      case "FAIL_SUBMISSION":
        return (
          <CSSTransition
            key={30}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <FailSubmission ansUserName={ansUserName} />
          </CSSTransition>
        );
      case "LOADING_DETAILS":
        return (
          <CSSTransition
            key={31}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <LoadingDetails ansUserName={ansUserName} />
          </CSSTransition>
        );
      default:
        return (
          <CSSTransition
            key={29}
            classNames={{
              enterActive: "animate__animated animate__fadeInRightBig",
              exitActive: "hide-div",
            }}
            unmountOnExit
            timeout={900}
          >
            <StepOne setCurrentStep={changeStep} qOneAns={qOneAns} />
          </CSSTransition>
        );
    }
  };
  return (
    <>
      <div
        className={
          quoteType === "light"
            ? "container mt-3 center quote-tab-light"
            : "container mt-3 center quote-tab-dark"
        }
      >
        <TransitionGroup>{renderSteps()}</TransitionGroup>
      </div>
    </>
  );
}

export default QuoteSteps;
