import React, { useState } from "react";
import { useTranslation } from "react-i18next";

import CurrencyInput from "../../../components/CurrencyInput/CurrencyInput";

import "./RemainingBalance.css";

function RemainingBalance({ setCurrentStep, ansRemainingBalance }) {
  const [remainingBalance, setRemainingBalance] = useState(ansRemainingBalance);
  const [haveError, setHaveError] = useState(false);

  const { t } = useTranslation();

  const nextBtnClick = () => {
    if (remainingBalance === "") {
      setHaveError(true);
    } else {
      setCurrentStep(
        "REMAINING_BALANCE",
        "MORTGAGE_INTEREST",
        remainingBalance
      );
    }
  };

  const inputOnChange = (value) => {
    setRemainingBalance(value);
    if (value !== "") {
      setHaveError(false);
    } else {
      setHaveError(true);
    }
  };

  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.REMAINING_BALANCE.head")}
          </h2>
          <h3 className="q-step-sub-text">
            {t("questionsStep.REMAINING_BALANCE.sub")}
          </h3>
        </div>
      </div>
      <div className="row mt-5 d-flex justify-content-center">
        <div className="col-sm-3">
          <div className="input-box-content">
            <div className="input-box-style-symbl">
              <CurrencyInput
                className="input-box-style"
                value={remainingBalance ? remainingBalance : ""}
                type="text"
                onChange={(e) => {
                  inputOnChange(e.target.value);
                }}
              />
            </div>
          </div>
          {haveError && (
            <div className="error-message">
              {t("questionsStep.REMAINING_BALANCE.error")}
            </div>
          )}
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            nextBtnClick();
          }}
        >
          <div className="step-q-button">{t("questionsStep.next")}</div>
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "REMAINING_BALANCE",
              "HOME_VALUE",
              ansRemainingBalance
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default RemainingBalance;
