import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import ZipCodeBox from "../../../components/ZipCodeBox/ZipCodeBox";
import validateStreetAddress from "../../../util/StreetAddressValidation";

import "./CurrentAddress.css";

function CurrentAddress({
  setCurrentStep,
  ansCurrentZipCode,
  ansCurrentStreetAddress,
}) {
  const [selectedZipCode, setSelectedZipCode] = useState(ansCurrentZipCode);
  const [selectedStreetAddress, setSelectedStreetAddress] = useState(
    ansCurrentStreetAddress
  );
  const [haveStreetError, setHaveStreetError] = useState(false);
  const [haveZipError, setHaveZipError] = useState(false);

  const { t } = useTranslation();

  const nextBtnClick = () => {
    if (validateStreetAddress(selectedStreetAddress)[1]) {
      setHaveStreetError(null);

      if (selectedZipCode !== null) {
        setCurrentStep("CURRENT_ADDRESS", "USER_EMAIL", {
          zipCode: selectedZipCode,
          streetAddress: selectedStreetAddress,
        });
        setHaveZipError(false);
      } else {
        setHaveZipError(true);
      }
    } else {
      setHaveStreetError(validateStreetAddress(selectedStreetAddress)[0]);
      console.log(validateStreetAddress(selectedStreetAddress)[0]);
    }
  };

  const changeCurrentValue = (value) => {
    setSelectedZipCode(value);
  };

  const inputOnChange = (value) => {
    setSelectedStreetAddress(value);

    if (validateStreetAddress(value)[1]) {
      setHaveStreetError(null);
    } else {
      setHaveStreetError(validateStreetAddress(value)[0]);
    }
  };

  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.CURRENT_ADDRESS.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-5 d-flex justify-content-center">
        <div className="col-sm-4">
          <input
            value={selectedStreetAddress ? selectedStreetAddress : ""}
            className="input-box-style"
            placeholder="123 Main St."
            onChange={(e) => {
              inputOnChange(e.target.value);
            }}
          />
          {haveStreetError && (
            <div className="error-message">{t(haveStreetError)}</div>
          )}
        </div>
      </div>
      <div className="row mt-5 d-flex justify-content-center">
        <div className="col-sm-4">
          <ZipCodeBox
            changeCurrentValue={changeCurrentValue}
            selectedOption={ansCurrentZipCode}
          />
          {haveZipError && (
            <div className="error-message">
              {t("questionsStep.CURRENT_ADDRESS.error")}
            </div>
          )}
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            nextBtnClick();
          }}
        >
          <div className="step-q-button">{t("questionsStep.next")}</div>
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "CURRENT_ADDRESS",
              "MORTGAGE_PAYMENT",
              ansCurrentZipCode
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default CurrentAddress;
