import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import DestinationLeaveBox from "../../../components/CityZipCodeBox/CityZipCodeBox";

import "./WhereBuy.css";

function WhereBuy({ setCurrentStep, ansWhereBuy }) {
  const [selectedLocation, setSelectedLocation] = useState(ansWhereBuy);
  const [haveError, setHaveError] = useState(false);

  const { t } = useTranslation();

  const nextBtnClick = () => {
    if (selectedLocation !== null) {
      setCurrentStep("WHERE_BUY", "CURRENT_ZIP_CODE", selectedLocation);
      setHaveError(false);
    } else {
      setHaveError(true);
    }
  };

  const changeCurrentValue = (value) => {
    setSelectedLocation(value);
    setCurrentStep("WHERE_BUY", "CURRENT_ZIP_CODE", value);
  };

  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.WHERE_BUY.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-5 d-flex justify-content-center">
        <div className="col-sm-4">
          <DestinationLeaveBox
            changeCurrentValue={changeCurrentValue}
            selectedOption={ansWhereBuy}
          />
          {haveError && (
            <div className="error-message">
              {t("questionsStep.WHERE_BUY.error")}
            </div>
          )}
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            nextBtnClick();
          }}
        >
          <div className="step-q-button">{t("questionsStep.next")}</div>
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("WHERE_BUY", "WORK_REAL_ESTATE", ansWhereBuy);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default WhereBuy;
