import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./LoanType.css";

function LoanType({ setCurrentStep, ansLoanType }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.LOAN_TYPE.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansLoanType === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_TYPE", "LOAN_GOAL", 0);
            }}
          >
            {ansLoanType === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.LOAN_TYPE.ans_1")}
          </div>
          <div
            className={
              ansLoanType === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_TYPE", "LOAN_GOAL", 2);
            }}
          >
            {ansLoanType === 2 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.LOAN_TYPE.ans_2")}
          </div>
        </div>
        <div className="col-sm-3">
          <div
            className={
              ansLoanType === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_TYPE", "LOAN_GOAL", 1);
            }}
          >
            {ansLoanType === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.LOAN_TYPE.ans_3")}
          </div>
          <div
            className={
              ansLoanType === 3
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_TYPE", "LOAN_GOAL", 3);
            }}
          >
            {ansLoanType === 3 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.LOAN_TYPE.ans_4")}
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansLoanType === 4
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("LOAN_TYPE", "LOAN_GOAL", 4);
            }}
          >
            {ansLoanType === 4 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.LOAN_TYPE.ans_5")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("LOAN_TYPE", "LOAN_GOAL", ansLoanType);
          }}
        >
          {ansLoanType !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("LOAN_TYPE", "STEP_1", ansLoanType);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default LoanType;
