import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./HowUseHome.css";

function HowUseHome({ setCurrentStep, ansHowUseHome, qOneAns, ansHomeType }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {qOneAns === 0
              ? t("questionsStep.HOW_USE_HOME.head")
              : t("questionsStep.HOW_USE_HOME.head_1")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansHowUseHome === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "HOW_USE_HOME",
                qOneAns === 0 ? "FIRST_TIME_USE" : "SERVED_MILITARY",
                0
              );
            }}
          >
            {ansHowUseHome === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HOW_USE_HOME.ans_1")}
          </div>
          <div
            className={
              ansHowUseHome === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "HOW_USE_HOME",
                qOneAns === 0 ? "FIRST_TIME_USE" : "SERVED_MILITARY",
                1
              );
            }}
          >
            {ansHowUseHome === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HOW_USE_HOME.ans_2")}
          </div>
          <div
            className={
              ansHowUseHome === 2
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep(
                "HOW_USE_HOME",
                qOneAns === 0 ? "FIRST_TIME_USE" : "SERVED_MILITARY",
                2
              );
            }}
          >
            {ansHowUseHome === 2 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.HOW_USE_HOME.ans_3")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "HOW_USE_HOME",
              qOneAns === 0 ? "FIRST_TIME_USE" : "SERVED_MILITARY",
              ansHowUseHome
            );
          }}
        >
          {ansHowUseHome !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "HOW_USE_HOME",
              ansHomeType === 4 ? "OWN_LAND" : "HOME_TYPE",
              ansHowUseHome
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default HowUseHome;
