import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./MortgagePayment.css";

function MortgagePayment({ setCurrentStep, ansMortgagePayment }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.MORTGAGE_PAYMENT.head")}
          </h2>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansMortgagePayment === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("MORTGAGE_PAYMENT", "CURRENT_ADDRESS", 0);
            }}
          >
            {ansMortgagePayment === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.MORTGAGE_PAYMENT.ans_1")}
          </div>
          <div
            className={
              ansMortgagePayment === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("MORTGAGE_PAYMENT", "CURRENT_ADDRESS", 1);
            }}
          >
            {ansMortgagePayment === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.MORTGAGE_PAYMENT.ans_2")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep(
              "MORTGAGE_PAYMENT",
              "CURRENT_ADDRESS",
              ansMortgagePayment
            );
          }}
        >
          {ansMortgagePayment !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep(
              "MORTGAGE_PAYMENT",
              "CURRENT_CREDIT_SCORE",
              ansMortgagePayment
            );
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default MortgagePayment;
