import React from "react";
import { useTranslation } from "react-i18next";

import { AiFillCheckCircle } from "react-icons/ai";

import "./OwnLand.css";

function OwnLand({ setCurrentStep, ansOwnLand }) {
  const { t } = useTranslation();
  return (
    <div>
      <div className="row d-flex justify-content-center mt-5">
        <div className="col-sm-7">
          <h2 className="q-step-head-text">
            {t("questionsStep.OWN_LAND.head")}
          </h2>
          <h3 className="q-step-sub-text">{t("questionsStep.OWN_LAND.sub")}</h3>
        </div>
      </div>
      <div className="row mt-3 d-flex justify-content-center">
        <div className="col-sm-3">
          <div
            className={
              ansOwnLand === 0
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("OWN_LAND", "HOW_USE_HOME", 0);
            }}
          >
            {ansOwnLand === 0 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.OWN_LAND.ans_1")}
          </div>
          <div
            className={
              ansOwnLand === 1
                ? "step-q-button step-q-button-active"
                : "step-q-button"
            }
            onClick={() => {
              setCurrentStep("OWN_LAND", "HOW_USE_HOME", 1);
            }}
          >
            {ansOwnLand === 1 && <AiFillCheckCircle />}{" "}
            {t("questionsStep.OWN_LAND.ans_2")}
          </div>
        </div>
      </div>
      <div className="row mt-4 d-flex justify-content-center">
        <div
          className="col-sm-2"
          onClick={() => {
            setCurrentStep("OWN_LAND", "HOW_USE_HOME", ansOwnLand);
          }}
        >
          {ansOwnLand !== null && (
            <div className="step-q-button">{t("questionsStep.next")}</div>
          )}
        </div>
        <p
          className="step-back-btn"
          onClick={() => {
            setCurrentStep("OWN_LAND", "HOME_TYPE", ansOwnLand);
          }}
        >
          {t("questionsStep.back")}
        </p>
      </div>
    </div>
  );
}

export default OwnLand;
